import { useCallback, useEffect, useState } from 'react';
import { useClickAway } from './useClickAway';
export const getCurrentOptions = ({ options, value }) => options.filter((opt) => opt !== value);
export function useDropdown(props) {
    const { value, options } = props;
    const { isClickedAway, setIsClickedAway, targetRef } = useClickAway();
    const [selected, setSelected] = useState(value);
    const [isOpen, setIsOpen] = useState(false);
    const [currentOptions, setCurrentOptions] = useState(options);
    useEffect(() => {
        if (targetRef.current) {
            setIsClickedAway(false);
            if (isOpen && isClickedAway) {
                setIsOpen(false);
            }
        }
    }, [isClickedAway, targetRef]);
    const onSelect = useCallback(() => {
        if (targetRef.current) {
            setIsOpen(!isOpen);
        }
    }, [isOpen]);
    const onSelectOption = useCallback((selectValue) => () => {
        setSelected(selectValue);
        setCurrentOptions(options);
        setIsOpen(false);
    }, [options]);
    return {
        selected,
        isOpen,
        currentOptions,
        selectBoxRef: targetRef,
        isClickedAway,
        onSelect,
        onSelectOption,
        setCurrentOptions,
        setSelected,
    };
}
