const randomColor = (firstLetter) => {
    const char = firstLetter.charCodeAt(0);
    switch (true) {
        case char % 4 === 0: {
            return `
        background: var(--green_500);
        border:1px solid var(--brand_green_800);
        &:hover{
          background: var(--brand_green_400);
          border:1px solid var(--brand_green_800);
        }
        &:active{
          background: var(--brand_green_600);
          border:1px solid var(--brand_green_1000);
        }
        &:focus{
          background: var(--green_600);
          border:1px solid var(--brand_green_1200);
        }
      `;
        }
        case char % 4 === 1: {
            return `
        background: var(--orange_500);
        border:1px solid var(--orange_800);
        &:hover{
          background: var(--orange_400);
          border:1px solid var(--orange_800);
        }
        &:active{
          background: var(--orange_600);
          border:1px solid var(--orange_1000);
        }
        &:focus{
          background: var(--orange_600);
          border:1px solid var(--orange_1200);
        }        
      `;
        }
        case char % 4 === 2: {
            return `
        background: var(--blue_500);
        border:1px solid var(--blue_800);
        &:hover{
          background: var(--blue_400);
          border:1px solid var(--blue_800);
        }
        &:active{
          background: var(--blue_600);
          border:1px solid var(--blue_800);
        }
        &:focus{
          background: var(--blue_600);
          border:1px solid var(--blue_1200);
        }
      `;
        }
        default: {
            return `
        background: var(--purplelight_500);
        border:1px solid var(--purplelight_800);
        &:hover{
          background: var(--purplelight_400);
          border:1px solid var(--purplelight_800);
        }
        &:active{
          background: var(--purplelight_600);
          border:1px solid var(--purplelight_1000);
        }
        &:focus{
          background: var(--purplelight_600);
          border:1px solid var(--purplelight_1200);
        }        
      `;
        }
    }
};
