import designToken from './designToken.json';
const TEXT_MODEL = designToken.TEXT;
const TOKEN_TYPO = TEXT_MODEL;
export const TYPOGRAPHY_MODEL = ({ type, weight }) => `
  font-size: ${TOKEN_TYPO.Type[type].size};
  line-height: ${TOKEN_TYPO.Type[type].lineHeight};
  font-weight: ${TOKEN_TYPO.Weight[weight]};
`;
export const FONT_STYLE = ({ type, weight, color }) => `
  ${TYPOGRAPHY_MODEL({ type, weight })}
  color:var(--${color});
`;
