import { useEffect, useRef, useState } from 'react';
export function useClickAway() {
    const targetRef = useRef(null);
    const [isClickedAway, setIsClickedAway] = useState(false);
    useEffect(() => {
        const clickAway = (e) => {
            if (targetRef.current) {
                setIsClickedAway(!e.composedPath().includes(targetRef.current));
            }
        };
        document.addEventListener('click', clickAway, false);
        return () => {
            document.removeEventListener('click', clickAway, false);
        };
    }, [targetRef]);
    return { targetRef, setIsClickedAway, isClickedAway };
}
