import styled from 'styled-components';
import { TYPOGRAPHY_MODEL } from '../../model/typography';
export const WrapSwitch = styled.span `
  display: inline-flex;
`;
export const SwitchLabel = styled.label `
  cursor: pointer;
  border-radius: 64px;

  ${({ theme, swtichSize }) => `
    position: relative;
    width: ${theme.size[swtichSize].width};
    height: ${theme.size[swtichSize].height};
    background: ${theme.status.off.enabled.track};

    &:before {
      position: absolute;
      right: ${theme.size[swtichSize].labelRight};
      z-index: 10;
      height: 100%;
      display: flex;
      align-items: center;
      content: 'OFF';
      color: ${theme.status.off.enabled.label};
      ${TYPOGRAPHY_MODEL({ type: theme.size[swtichSize].type, weight: 'medium' })}
    }
    &:after {
      position: absolute;
      width: ${theme.size[swtichSize].handle};
      height:  ${theme.size[swtichSize].handle};
      left:${theme.size[swtichSize].handleTop};
      top: ${theme.size[swtichSize].handleLeft};
      border-radius: 50%;
      box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.04), 0px 2px 7px rgba(0, 0, 0, 0.06);
      background:${theme.status.off.enabled.handle};
      content: '';
    }
    &:hover {
      background: ${theme.status.off.hovered.track};
    }
    &:disabled {
      background: ${theme.status.off.disabled.track};
      &:before {
        color: ${theme.status.off.disabled.label};
      }
      &:after {
        background:${theme.status.off.disabled.handle};
      }
    }
  `};
`;
export const SwitchRadio = styled.input `
  position: absolute;
  width: 0;
  height: 0;
  font-size: 0;
  visibility: hidden;
  opacity: 0;
  ${({ theme, swtichSize }) => `
  &:checked {
    + ${SwitchLabel} {
      background: ${theme.status.on.enabled.track};
      &:before {
        content: 'ON';
        left:  ${theme.size[swtichSize].labelLeft};
        right: auto;
      }
      &:after {
        left: auto;
        right:  ${theme.size[swtichSize].handleRight};
      }
      &:hover {
        background: ${theme.status.on.hovered.track};
      }
    }
  }
  &:disabled {
    + ${SwitchLabel} {
      background: ${theme.status.off.disabled.track};
      &:after {
        opacity: 0.5; 
      }
      &:before {
        color: ${theme.status.off.disabled.label};
      } 
    }
    &:checked  + ${SwitchLabel} {
      background: ${theme.status.on.disabled.track};
      &:before {
        color: ${theme.status.on.disabled.label};
      } 
    }
  }
  `}
`;
