import styled, { css, keyframes } from 'styled-components';
const skeletonAnimation = (_width) => keyframes `
0% {
      background-position: -${_width} 0px;
} 
100%{
      background-position: ${_width} 0px;
}
`;
const margin = ({ mg, mt, mr, mb, ml }) => css `
  ${mg && `margin:${mg};`}
  ${mt && `margin-top:${mt};`}
  ${mr && `margin-right:${mr};`}
  ${mb && `margin-bottom:${mb};`}
  ${ml && `margin-left:${ml};`}
`;
const position = ({ top, left, bottom, right }) => css `
  ${top && `top:${top};`}
  ${left && `left:${left};`}
  ${right && `right:${right};`}
  ${bottom && `bottom:${bottom};`}
`;
export const Skeleton = styled.div `
  ${({ position }) => (position ? `position:${position};` : `position:relative`)};
  ${({ top, left, bottom, right }) => position({ top, left, bottom, right })}
  ${({ _width }) => `width: ${_width};`};
  ${({ _height }) => `height: ${_height};`};
  ${({ mg, mt, mr, mb, ml }) => margin({ mg, mt, mr, mb, ml })}
  ${({ rd }) => rd && `border-radius: ${rd}`};
  background-color: #eaeaea;
  &::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    ${({ rd }) => rd && `border-radius: ${rd}`};
    background-image: linear-gradient(90deg, #eaeaea 0%, #f4f4f4 50%, #eaeaea 100%);
    background-size: ${({ _width, _height }) => `${_width} ${_height}`};
    background-repeat: no-repeat;
    animation: ${({ _width }) => skeletonAnimation(_width)} 1s linear infinite;
  }
`;
